*{
    margin: 0;padding: 0;box-sizing: border-box;
}
.container{
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
background-color: rgb(19, 5, 29);
}
.field{
    height: 500px;
    width:330px;
    display: flex;box-shadow:inset 1px 2px 4px  rgb(189, 189, 234),inset -1px -1px 4px rgb(149, 149, 238);
    justify-content: center;
    align-items: center;flex-direction: column;
}
.infopart{
    height:140px;
    width: 100%;font-family: sans-serif;
    display: flex;font-size: 16px;color: white;
    align-items:center;flex-direction: column;
}
.infopart p{
    font-size: 17px;
    color: white;font-weight: 550;

}
.h4{
    position:relative;height:40px;width:90%;display: grid;
    place-items: center;
    background-color:honeydew;box-shadow:inset 0.1px 1px 3px gray,inset -1px -3px 3px gray;
    border-radius: 4px;color: black;
    top:20px;
}
.infopart span{
    color: red;
}
.field button{
    height:40px;
    width: 160px;
    border:  1px solid black;;
    outline:0;
    color: white;
 font-size: 15px;
    border-radius: 6px;
    margin-top: 15px;cursor: pointer;background-color:lime;transition: 0.3s;
}
.field button:hover{
    border: 0px ;
    box-shadow: 1px 2px 4px rgb(105, 98, 98);
}
.box{
    height:300px;
    width: 300px;
    display: flex;
    justify-content: center;outline:none;border: 0px;
    align-items: center;flex-direction: column;   border: 2px solid black;
   box-shadow: 1px 2px 4px black,-1px -1px 4px black;

}
.r1,.r2,.r3{
    height:100px;
    width: 300px;
    display: flex;
    justify-content: center;
    align-items: center;    
}
.e11,.e12,.e13,.e21,.e22,.e23,.e31,.e32,.e33{
    height:100px;font-size: 33px;
    width: 100px;
    display: flex;cursor: pointer;color:dodgerblue;
    justify-content: center;
    align-items: center;    border: 1px solid black;user-select:none;
}
.e11{
 border-top: 0;border-left:0;
}
.e12{
    border-top: 0;
   }
   .e13{
    border-top: 0;border-right:0;
   }
   .e21{
   border-left:0;
   }
 
   .e23{
   border-right:0;
   }
   .e31{
    border-bottom: 0;border-left:0;
   }.e32{
    border-bottom:0;
   }
   .e33{
    border-bottom: 0;border-right:0;
   }


   @media only screen and (max-width:600px){
    .container{
        height: 100vh;
        width: 100vw;
      position: relative;
    }
    .field{
        height: 500px;
        width:90%;
      position: absolute;
      top:12%;

      background-color:rgb(208, 231, 208);
    }
    .infopart P{
        color: black;
    }

    .box{
        height:300px;
        width: 300px;
        display: flex;
        justify-content: center;outline:none;border: 0px;
        align-items: center;flex-direction: column;   border: 2px solid black;
       box-shadow: 1px 6px 14px black,-1px -1px 4px black;

    }
   }